<template>
  <div class="mwsbtn-group">
    <button v-for="btn of buttons" :key="btn.ID" small @click="changeBtnValue(btn.ID)" :style="{width: autoWidth}">
      {{btn.VALUE}}
    </button>
  </div>
</template>

<script>
export default {
  name: 'buttonsPanel',
  props:{
    buttons: Array
  },
  data() {
    return{
      
    }
  },
  methods:{
    changeBtnValue(val){
      this.btn = val;
      this.$store.dispatch('CHANGE_SELECTED_BTN', {val: val});
    }
  },
  computed:{
    autoWidth(){
      return 100/this.buttons.length+'%';
    }
  }
}
</script>

<style scoped>
  .mwsbtn-group button {
    background-color: darkgray; /* Green background */
    border-right: 1px solid black; /* Green border */
    color: black; /* White text */
    padding: 10px 24px; /* Some padding */
    cursor: pointer; /* Pointer/hand icon */
    float: left; /* Float the buttons side by side */
  }

  .mwsbtn-group button:last-child {
    border-right: none; /* Prevent double borders */
    border-radius: 0px 25px 25px 0px;
  }
  .mwsbtn-group button:first-child {
    border-radius: 25px 0px 0px 25px;
  }

  /* Clear floats (clearfix hack) */
  .mwsbtn-group:after {
    content: "";
    clear: both;
    display: table;
  }

  /* Add a background color on hover */
  .mwsbtn-group button:hover {
    background-color: lightgray;
  }
  .mwsbtn-group{
    margin-bottom: 20px;
  }
</style>