<template>
  <div>
    <v-select
      :multiple="fieldData.multiple"
      :label="fieldData.title"
      :items="fieldData.items"
      item-text="VALUE"
      item-value="ID"
      v-model="value"
      outlined
      dense
    ></v-select>
  </div>
</template>

<script>
export default {
  name: 'app_select',
  props:{
    fieldData: Object
  },
  data(){
    return{

    }
  },
  computed:{
    value(){
      return String(this.$store.getters.GET_FIELD_VALUE(this.fieldData.code));
    }
  }
}
</script>