<template>
  <div>
      <v-menu
        v-model="menu"
        :close-on-content-click="false"
        :nudge-right="40"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            :value="date|dateToLocal"
            :label="fieldData.title"
            prepend-icon="mdi-calendar"
            readonly
            dense
            outlined
            v-bind="attrs"
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="date"
          no-title
          @input="menu = false"
          scrollable
          locale="ru-RU"
          first-day-of-week="1"
        >
        </v-date-picker>
      </v-menu>
  </div>
</template>

<script>
export default {
  name: 'date',
  props:{
    fieldData: Object
  },
  data() {
    return{
      menu: false,
    }
  },
  filters:{
    dateToLocal(date){
      return date.split('-').reverse().join('.');
    }
  },
  computed:{
    date:{
      get(){
        return this.$store.getters.GET_FIELD_VALUE(this.fieldData.code).split('.').reverse().join('-');
      },
      set(val){
        this.$store.dispatch('FIELD_VALUE_SETTER', {name:this.fieldData.code, value: val.split('-').reverse().join('.')});
      }
    }
  },
  methods:{
    saveData(){

    }
  }
}
</script>