<template>
  <div>
    <v-checkbox
      :label="fieldData.title"
      v-model="value"
    ></v-checkbox>
  </div>
</template>

<script>
export default {
  name: 'boolean',
  props: {
    fieldData: Object
  },
  data() {
    return {

    }
  },
  computed:{
    value(){
      return this.$store.getters.GET_FIELD_VALUE(this.fieldData.code);
    }
  }
}
</script>