<template>
  <div>
    <v-textarea
      :label="fieldData.title"
      v-model="value"
      dense
      outlined
    ></v-textarea>
  </div>
</template>

<script>
export default {
  name: 'app_textarea',
  props:{
    fieldData: Object
  },
  data() {
    return {

    }
  },
  computed:{
    value(){
      return this.$store.getters.GET_FIELD_VALUE(this.fieldData.code);
    }
  }
}
</script>