<template>
  <div>
    <v-autocomplete
      :multiple="fieldData.multiple"
      :label="fieldData.title"
      :items="fieldData.items"
      item-value="value"
      item-text="text"
      v-model="value"
      dense
      outlined
    ></v-autocomplete>
  </div>
</template>

<script>
export default {
  name: 'autocomplete',
  props:{
    fieldData: Object
  },
  data(){
    return{

    }
  },
  computed:{
    value(){
      return String(this.$store.getters.GET_FIELD_VALUE(this.fieldData.code));
    }
  }
}
</script>